@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700');

html, body { height: 100%; min-width: 320px;}
body { margin: 0px auto; -webkit-text-size-adjust: none; min-width: 320px; margin: 0 auto; font-family: "Open Sans", sans-serif; line-height: 125%; color: #252525; text-align: center;}

h1, h2, h3, h4, h5, h6 { font-family: "Open Sans", sans-serif; color: #252525; line-height: 125%; }

h1 { font-size: 3rem; margin: 20px 0; font-weight: 500; text-transform:uppercase; letter-spacing: 5px; }
h2 { font-size: 2rem; margin: 10px 0;text-transform:uppercase; letter-spacing: 2px;  }
h3 { font-size: 1.25rem; margin: 10px 0; text-transform:uppercase; letter-spacing: 2px;}
h4 { font-size: 16px; margin: 10px 0; }
h5 { font-size: 14px; margin: 10px 0; font-weight:900;}
h6 { font-size: 12px; margin: 10px 0; }

ul, ol { margin: 0px 0 0px 0px; padding: 0; }
ul li, ol li { margin: 0px 0 0 0px; padding: 0px 0 0 0px; line-height: 140%; }

.btn { cursor: pointer; transition: background-color 0.35s 0.25s, color 0.35s 0.25s; font-size: 0.75rem; padding: 12px 15px; font-family: "Work Sans", sans-serif; display: inline-block; background-color: #555; position: relative; min-width: 120px; color: #fff; text-decoration: none; font-weight: 400; text-align: center; border:1px solid #555;text-transform: uppercase;text-shadow: none;border-radius: 5px; letter-spacing: 3px;}
.btn:hover { transition: background-color 0.35s 0.25s, color 0.35s 0.25s; background-color: transparent;color:#555; }
.btn.alt { color: #555;border:1px solid #555;}
.btn.alt:hover {color: #fff; }

input[type=text] { height:0;outline: none; border: 0px; color: #555; padding: 22px 20px; box-sizing: border-box; line-height: 100%; min-width: 325px; font-weight: 500; font-family: "Open Sans", sans-serif; font-size: 14px;display: inline-block; }

.formGroup{display: inline-block;}
.formGroup input[type=text]{border:1px solid #555; border-radius: 5px 0 0 5px; vertical-align: middle;max-height: 46px;}
.formGroup .btn{border-radius: 0 5px 5px 0px; vertical-align: middle;}

#employee-directory{position: absolute;top:50%;left:50%;transform:translate(-50%, -50%);display: inline-block;}

.resultDiv ul{margin-bottom:50px;}
